import React, { Component } from 'react';
import moment from 'moment'

export default class Home extends Component {

  constructor(props) {
    super(props)

    const timesString = localStorage.getItem('times')

    const times = JSON.parse(timesString) || []

    this.state = {
      times: times
    }
  }
  addTime = () => {
    return this.props.history.push('/manageTime')
  }

  calculateAttributes = (timesArray) => {
    return timesArray.map((objectAsString) => {
      const timeEntry = JSON.parse(objectAsString)

      let startDate = moment(timeEntry.startTime)
      let endDate = moment(timeEntry.endTime)

      const diffHours = endDate.diff(startDate, 'hours')
      const diffMinutes = moment.duration(endDate.diff(startDate)).minutes()

      const startDateString = startDate.format('LL')

      return { startDate: startDateString, totalHours: diffHours, minutes: diffMinutes }

    })
  }

  calculateTotalHours = (timesArray) => {
    const diffMin = timesArray.reduce((acc, objString) => {
      const timeEntry = JSON.parse(objString)

      let startDate = moment(timeEntry.startTime)
      let endDate = moment(timeEntry.endTime)

      const diffMin = endDate.diff(startDate, 'minutes')
      acc = acc + diffMin
      return acc
    }, 0)

    return {
      totalHours: Math.floor(moment.duration(diffMin, 'minutes').asHours()),
      minutes: moment.duration(diffMin, 'minutes').minutes(),
      totalMinutes: diffMin
    }
  }


  deleteEntry = (timesArray, index) => () => {
    console.log(timesArray, index)

    timesArray.splice(index, 1)
    localStorage.setItem('times', JSON.stringify(timesArray))

    this.setState({ times: timesArray })

    return
  }
  render() {
    const times = this.state.times

    const totalHours = this.calculateTotalHours(times)
    var formatter = new Intl.NumberFormat("usa", {
      style: "currency",
      currency: "USD"
    });
    return (
      <table className="table is-striped is-fullwidth">
        <thead>
          <tr>
            <th><abbr title="Date">Date</abbr></th>
            <th><abbr title="Total Hours">Duration</abbr></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {
            this.calculateAttributes(times).map((t, index) => {
              return (
                <tr>
                  <td>{t.startDate}</td>
                  <th>{t.totalHours}h {t.minutes}min</th>
                  <th style={{ cursor: "pointer", textAlign: "center" }} >
                    <div onClick={this.deleteEntry(times, index)} >
                      <i className="fa fa-trash" style={{ "font-size": "20px" }}></i>
                    </div>
                  </th>
                </tr>
              )
            })
          }

        </tbody>
        <tfoot>
          <td>
            {formatter.format(totalHours.totalMinutes * 0.2)}
          </td>
          <th>
            {totalHours.totalHours}h {totalHours.minutes}min
          </th>
          <td style={{ textAlign: "center" }}>
            <a className="button is-primary" onClick={this.addTime}>
              <span className="icon">
                <i className="fas fa-plus"></i>
              </span>
              <span>Add Time</span>
            </a>
          </td>
        </tfoot>
      </table>
    )
  }
}


