import React, { Component } from 'react';
import moment from 'moment'

export default class ManageTime extends Component {

  constructor(props) {
    super(props);
    this.state = {
      startTime: null,
      endTime: null
    };
  }

  goHome = () => {
    return this.props.history.push('/')
  }
  saveTime = (e) => {
    const timesString = localStorage.getItem('times')
    const times = JSON.parse(timesString) || []

    times.push(JSON.stringify({
      startTime: moment(this.state.startTime).toJSON(),
      endTime: moment(this.state.endTime).toJSON()
    }))

    localStorage.setItem('times', JSON.stringify(times))
    return this.props.history.push('/')
  }


  onChangeStartTime = (e) => {
    this.setState({ startTime: e.target.value })
  }
  onChangeEndTime = (e) => {
    this.setState({ endTime: e.target.value })

  }

  render() {
    return (
      <div className='container'>
        <section style={{ "padding": "10px" }}>
          <div className="field">
            <label className="label is-medium">Start</label>
            <div className="control">
              <input
                className='input is-medium'
                type="datetime-local" id="from"
                name="from" value={this.state.startTime} onChange={this.onChangeStartTime} />
            </div>
          </div>
          <div className="field">
            <label className="label is-medium">End</label>
            <div className="control">
              <input
                className='input is-medium'
                type="datetime-local" id="to"
                name="to" value={this.state.endTime} onChange={this.onChangeEndTime} />
            </div>
          </div>
          <div className="field is-grouped is-grouped-right">
            <div className="control">
              <button className="button is-primary" onClick={this.saveTime}>Save</button>
            </div>
            <div className="control">
              <button className="button" onClick={this.goHome}>Cancel</button>
            </div>
          </div>
        </section>
      </div >
    )
  }
}


